.port-tags{
  background-color:#333;
  color:white;
  -webkit-box-shadow: 1px 1px 0 black;
  box-shadow:         1px 1px 0 black;
  display:inline-block;
  opacity: 0;
  margin:2px;
  font-size: 22px;
  padding:0 8px 3px 8px;
  border-width:1px 1px 2px 1px;
  border-style:solid;
  border-color: #0d0d0d;
  border-radius: 3px;
}
.card:hover li{
  -webkit-transform: translateY(20px);
      -ms-transform: translateY(20px);
          transform: translateY(20px);
  opacity: 1;
  -webkit-transition:opacity, -webkit-transform .285s cubic-bezier(.5,1.5,.5,1.5);
  transition:opacity, -webkit-transform .285s cubic-bezier(.5,1.5,.5,1.5);
  -o-transition:transform .285s cubic-bezier(.5,1.5,.5,1.5), opacity;
  transition:transform .285s cubic-bezier(.5,1.5,.5,1.5), opacity;
  transition:transform .285s cubic-bezier(.5,1.5,.5,1.5), opacity, -webkit-transform .285s cubic-bezier(.5,1.5,.5,1.5);
}
.card:hover #li2{-webkit-transition-delay:.2s;-o-transition-delay:.2s;transition-delay:.2s;}
.card:hover #li3{-webkit-transition-delay:.4s;-o-transition-delay:.4s;transition-delay:.4s;}
.card:hover #li4{-webkit-transition-delay:.6s;-o-transition-delay:.6s;transition-delay:.6s;}
.card:hover #li5{-webkit-transition-delay:.8s;-o-transition-delay:.8s;transition-delay:.8s;}
.card:hover #li6{-webkit-transition-delay:1.0s;-o-transition-delay:1.0s;transition-delay:1.0s;}
.card:hover #li7{-webkit-transition-delay:1.2s;-o-transition-delay:1.2s;transition-delay:1.2s;}
.card:hover #li8{-webkit-transition-delay:1.4s;-o-transition-delay:1.4s;transition-delay:1.4s;}
.card:hover #li9{-webkit-transition-delay:1.6s;-o-transition-delay:1.6s;transition-delay:1.6s;}
.card:hover #li10{-webkit-transition-delay:1.8s;-o-transition-delay:1.8s;transition-delay:1.8s;}
.card:hover #li11{-webkit-transition-delay:2.0s;-o-transition-delay:2.0s;transition-delay:2.0s;}



