/* portfolio menu section */
.btn-collapse{
  display:none; 
  margin-top:12px;
  margin-left:20px;
}
.portBtn{
  background-color: transparent !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;

}
/* collapseable button for portfolio images */
#portCollapse{
  font-size:22px;
  line-height: 36px;
  font-weight:700;
} 
.sectionPort, .sectionBio, .sectionContact{
  -ms-flex-pack: left;
      justify-content: left;
  margin-top:80px;
  margin-bottom:35px;
  -ms-flex-pack: center;
      justify-content: center;
}
.fal{
  color:white;
}
.listStyle{
  font-size: 16px;
  list-style-type: none;
  cursor: pointer;
}
.center{
  text-align: center;
}
.btn-group{
  margin-bottom: 20px;
  -ms-flex-flow:wrap;
      flex-flow:wrap;
  -ms-flex-pack: center;
      justify-content: center;
}
.btn-group>.btn:focus{
  background-color:#cc0000;
}
.myIcons{
  height:18px;
}
.myIcons2{
  height:42px;
}
.myIcons3{
  height:35px;
}
.myIcons4{
  height:25px;
}
.show{display:initial;}
.hide{
  display:none;
}

@media screen and (max-width:910px){
  .btn-collapse{
    display:inline-block; 
  }  
  .btn-group{
    display: none;
  }
} 
@media screen and (max-width:576px){
  .btn-collapse{
    display:inline-block; 
  } 
}


